
const intialState  = {
    todos:[]
}


const reducer = (state=intialState,action) => {

    if(action.type === 'GET_DATA'){
        return{
            todos:action.payload
        }
    }
    
   switch(action.type){
       case 'ADD_TODO':
        
           return{
               ...state,
               todos:[...state.todos,action.payload]
           }
        case 'DELETE_TODOS':
            return{
                ...state,
                todos:state.todos.filter(todo => todo.id !== action.payload)
            }
       default:
           return state;
   }
   
}


export default reducer